@import "font";
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #f2f6f9;
}

body,
html,
.content {
  height: 100%;
}

.content {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
